<template>
  <div id="data-view" ref="dataView">
    <div class="block-header"></div>
    <div class="block-content">
      <left-container
        :cameraInfo="camera"
        :regionStatistics="regionStatistics"
        :cabinet="cabinet"
        :hatMap="hatMap"
        :maskMap="maskMap"
        :clothesMap="clothesMap"
        :certificateMap="certificateMap"
        :temperatureMap="temperatureMap"
        :humidityMap="humidityMap"

      />
      <center-container
        :totalJoinOrg="totalJoinOrg"
        :realTimeOrgJoinInfo="realTimeOrgJoinInfo"
        :regionStatistics="regionStatistics"
      />
      <right-container
        :cameraInfo="camera"
        :realTimeOrgJoinInfo="realTimeOrgJoinInfo"
        :companyRatio="companyRatio"
        :shcy="shcy"
        :zgst="zgst"
        :xxst="xxst"
        :jtjc="jtjc"
        :ygcj="ygcj"
        :all="all"
        :lastWeekIncr="lastWeekIncr"
      />
    </div>
  </div>
</template>

<script>
import leftContainer from '../custom/leftContainer'
import rightContainer from '../custom/rightContainer'
import centerContainer from '../custom/centerContainer'

import {
  getRealTimeTotald,
  getCabinet,
  getTotalOrgStatistics,
  getRegionStatistics,
  getRealTimeOrgJoin,
  getCategoryJoinTotal,
  getCompanyRatio,

  // 摄像头接入
  getsocialCaterJoinTotal,
  getstaffDiningHallCaterJoinTotal,
  getschoolDiningHallCaterJoinTotal,
  getshiningWorkshopCaterJoinTotal,
  getunionDinnerCaterJoinTotal,
  getallJoinTotal,
  getlastWeekIncr,

  // 白色厨师帽 白色口罩 未穿白色厨师服
  getCookHat,
  getCookMask,
  getCookClothes,

  // 统计当天 湿度 温度 持证上岗 告警
  getDegreeHumidity,
  getDgreeTemperature,
  getDegreeCertificate

} from '../../service/service'
const success = '0000'
export default {
  name: 'DataView',
  components: {
    leftContainer,
    centerContainer,
    rightContainer
  },
  data () {
    return {
      camera: {
        offline: 0,
        online: 0,
        onlineRatio: '100',
        total: 0
      },
      regionStatistics: [], // 地区接入数量
      realTimeOrgJoinInfo: [], // 实时接入信息
      totalJoinOrg: {
        // 总接入数量
        all: 0,
        todayTotal: 0,
        previousTotal: 0
      },
      // cameraTypeInfo: {
      //   categoryTotal: [],
      //   lastWeekCameraTotal: 0,
      //   lastWeekOrgTotal: 0
      // },
      cabinet: {}, // 消毒柜、留样柜、紫外灯
      cookInfo: {}, // 消毒柜、留样柜、紫外灯
      companyRatio: {
        companyRatio: {},
        companyTotal: {}
      },
      shcy: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      zgst: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      xxst: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      jtjc: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      ygcj: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      all: {
        currentWeekTotal: 0,
        incrRatio: '',
        onlineRatio: '',
        total: 0
      },
      lastWeekIncr: {
        lastWeekCameraTotal: 0,
        lastWeekOrgTotal: 0
      },
      hatMap: [],
      maskMap: [],
      clothesMap: [],

      certificateMap: [],
      temperatureMap: [],
      humidityMap: []
    }
  },
  mounted () {
    this.timer = setInterval(this.init, 120 * 1000)
    window.addEventListener('resize', this.changeViewZoom)
    this.changeViewZoom()
  },
  created () {
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.timer)
    window.removeEventListener('resize', this.changeViewZoom)
  },
  methods: {
    init () {
      const self = this
      self.getRealTimeTotald()
      self.getCabinet()
      self.getTotalOrgStatistics()
      self.getRegionStatistics()
      self.realTimeOrgJoinFun()
      // self.getCategoryJoinTotal()
      self.getCompanyRatioFun()

      // 摄像头分类接入统计
      self.getsocialCaterJoinTotal()
      self.getstaffDiningHallCaterJoinTotal()
      self.getschoolDiningHallCaterJoinTotal()
      self.getunionDinnerCaterJoinTotal()
      self.getshiningWorkshopCaterJoinTotal()
      self.getallJoinTotal()
      self.getlastWeekIncr()

      // 白色厨师帽 白色口罩 未穿白色厨师服
      self.getCookHat()
      self.getCookMask()
      self.getCookClothes()

      //
      self.getDegreeHumidity()
      self.getDegreeCertificate()
      self.getDgreeTemperature()
    },
    // 实时摄像头统计
    getRealTimeTotald () {
      getRealTimeTotald().then(res => {
        const { code, data } = res
        if (code === success) {
          this.camera = {
            offline: data.offline || 0,
            online: data.online || 0,
            onlineRatio: data.onlineRatio || '100',
            total: data.total || 0
          }
        }
      })
    },

    getCabinet () {
      getCabinet().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cabinet = data
        }
      })
    },
    // 共接入单位统计
    getTotalOrgStatistics () {
      getTotalOrgStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          let all = (parseInt(data.todayTotal || 0) + data.previousTotal || 0)
          this.totalJoinOrg = {
            all: all,
            todayTotal: data.todayTotal || 0,
            previousTotal: data.previousTotal || 0
          }
        }
      })
    },
    // 各地市接入单位数量统计排行
    getRegionStatistics () {
      getRegionStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          if (data && data.length) {
            const maxLen = data[0].count
            data.map(item => {
              if (item.count) {
                item.width = (item.count / maxLen * 100) + '%'
              } else {
                item.width = '10px'
              }
            })
          }
          this.regionStatistics = data
        }
      })
    },
    // 实时单位接入统计（按单位的create_time来计算）
    realTimeOrgJoinFun () {
      getRealTimeOrgJoin().then(res => {
        const { code, data } = res
        if (code === success) {
          this.realTimeOrgJoinInfo = data && data.length ? data : []
        }
      })
    },

    // -------------------------------------------------------------------------修改
    // 摄像头分类接入统计--社会餐饮
    getsocialCaterJoinTotal () {
      getsocialCaterJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.shcy = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--职工食堂
    getstaffDiningHallCaterJoinTotal () {
      getstaffDiningHallCaterJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.zgst = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--学校食堂
    getschoolDiningHallCaterJoinTotal () {
      getschoolDiningHallCaterJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.xxst = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--集体聚餐
    getunionDinnerCaterJoinTotal () {
      getunionDinnerCaterJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.jtjc = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--阳光车间
    getshiningWorkshopCaterJoinTotal () {
      getshiningWorkshopCaterJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.ygcj = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--全部
    getallJoinTotal () {
      getallJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.all = {
            currentWeekTotal: data.currentWeekTotal || 0,
            incrRatio: data.incrRatio || '',
            onlineRatio: data.onlineRatio || '',
            total: data.total || 0
          }
        }
      })
    },
    // 摄像头分类接入统计--上抽
    getlastWeekIncr () {
      getlastWeekIncr().then(res => {
        const { code, data } = res
        if (code === success) {
          this.lastWeekIncr = data
          console.log('lastWeekIncr....', data)
        }
      })
    },

    // 白色厨师帽 白色口罩 未穿白色厨师服
    getCookHat () {
      getCookHat().then(res => {
        const { code, data } = res
        if (code === success) {
          this.hatMap = data || []
        }
      })
    },
    // 白色厨师帽 白色口罩 未穿白色厨师服
    getCookMask () {
      getCookMask().then(res => {
        const { code, data } = res
        if (code === success) {
          this.maskMap = data || []
        }
      })
    },
    // 白色厨师帽 白色口罩 未穿白色厨师服
    getCookClothes () {
      getCookClothes().then(res => {
        const { code, data } = res
        if (code === success) {
          this.clothesMap = data || []
        }
      })
    },

    // 统计当天 湿度 告警
    getDegreeHumidity () {
      getDegreeHumidity().then(res => {
        const { code, data } = res
        if (code === success) {
          this.humidityMap = data || []
        }
      })
    },
    // 统计当天 温度 告警
    getDgreeTemperature () {
      getDgreeTemperature().then(res => {
        const { code, data } = res
        if (code === success) {
          this.temperatureMap = data || []
        }
      })
    },
    // 统计当天 持证上岗 告警
    getDegreeCertificate () {
      getDegreeCertificate().then(res => {
        const { code, data } = res
        if (code === success) {
          this.certificateMap = data || []
        }
      })
    },
    // -------------------------------------------------------------------------修改
    getCompanyRatioFun () {
      getCompanyRatio().then(res => {
        const { code, data } = res
        if (code === success) {
          this.companyRatio = data
        }
      })
    },

    changeViewZoom () {
      let docEle = document.documentElement
      let dataViewEle = this.$refs.dataView
      if (dataViewEle) {
        let screenRatio = docEle.clientWidth / docEle.clientHeight
        if (screenRatio >= 32 / 9) {
          dataViewEle.style.zoom = (docEle.clientHeight / 2160).toFixed(3)
          dataViewEle.style.marginTop = '0'
        } else {
          dataViewEle.style.zoom = (docEle.clientWidth / 7680).toFixed(3)
          dataViewEle.style.marginTop = ((docEle.clientHeight * 7680 / docEle.clientWidth - 2160) / 2).toFixed(3) + 'px'
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
#data-view {
  width: 7680px;
  height: 2160px;
  margin: 0 auto;
  padding: 0;
  color: #fff;
  background: url("../../assets/img/bg2.jpg") no-repeat center;
  background-size:100%;
  display: flex;
  flex-direction: column;
  .block-content {
    width: 100%;
    height: 1780px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 98px;
  }
  .block-header {
    width: 100%;
    display: -webkit-box;
    -webkit-box-flex: 1;
    flex: 1;
  }
}
</style>
