<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const options = {
  tooltip: {
    trigger: 'axis',
    textStyle: {
      align: 'left'
    }
  },
  grid: {
    top: '100', // 距上边距
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  calculable: false,
  legend: {
    data: ['温度', '湿度', '持证上岗'],
    itemGap: 60,
    textStyle: {
      color: '#e9ebee',
      fontSize: 20
    },
    right: '5%',
    padding: [30, 10]
  },
  xAxis: [
    {
      type: 'category',
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#fff',
          align: 'center'
        }
      }
    }
  ],
  yAxis: [
    {
      type: 'value',

      axisLabel: {
        formatter: '{value} ',
        textStyle: {
          color: '#fff',
          align: 'right'
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#00e4ff',
          opacity: 0.2
        }
      }
    }
  ],
  series: [
    {
      name: '温度',
      type: 'custom',
      data: [],
      itemStyle: {
        normal: {
          color: '#18e4c5'
        }
      }
    },
    {
      name: '湿度',
      type: 'custom',
      data: [],
      itemStyle: {
        normal: {
          color: '#3e93f5'
        }
      }
    },
    {
      name: '持证上岗',
      type: 'line',
      data: [],
      itemStyle: {
        normal: {
          color: '#ffb017'
        }
      }
    }
  ]
}

export default {
  name: 'TemperatureHumidity',
  props: {
    certificateMap: Array,
    temperatureMap: Array,
    humidityMap: Array
  },
  data () {
    return {
      option: {}
    }
  },
  computed: {
    certificateMapLen () {
      let total = 0
      if (this.certificateMap && this.certificateMap.length) {
        this.certificateMap.map(item => {
          total += (parseInt(item))
        })
      }
      return total
    },
    temperatureMapLen () {
      let total = 0
      if (this.temperatureMap && this.temperatureMap.length) {
        this.temperatureMap.map(item => {
          total += (parseInt(item))
        })
      }
      return total
    },
    humidityMapLen () {
      let total = 0
      if (this.humidityMap && this.humidityMap.length) {
        this.humidityMap.map(item => {
          total += (parseInt(item))
        })
      }
      return total
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },

    _isCenter (values, idx) {
      return !values || values[idx] === undefined || values[idx] === 0
    },
    setHumidityMapChartData () {
      const self = this
      if (self.humidityMap) {
        options.series.map(item => {
          if (item.name === '温度') {
            item = {
              name: '温度',
              type: 'custom',
              data: self.humidityMap,
              itemStyle: {
                normal: {
                  color: '#18e4c5'
                }
              },
              renderItem: function (params, api) {
                let coord = api.coord([api.value(0), api.value(1)])
                let size = api.size([1, api.value(1)])
                let width = size[0] * 0.35
                let offset = 0
                if (self._isCenter(self.temperatureMap, api.value(0))) {
                  offset = -width / 2
                } else {
                  offset = -size[0] * 0.10 / 2 - width
                }
                return {
                  type: 'rect',
                  shape: {
                    x: coord[0] + offset,
                    y: coord[1],
                    width: width,
                    height: size[1]
                  },
                  style: api.style()
                }
              }
            }
          }
          this.chart.setOption(options)
        })
      }
    },
    setTemperatureMapChartData () {
      const self = this
      if (self.temperatureMap) {
        options.series.map(item => {
          if (item.name === '湿度') {
            item = {
              name: '温度',
              type: 'custom',
              data: self.temperatureMap,
              itemStyle: {
                normal: {
                  color: '#18e4c5'
                }
              },
              renderItem: function (params, api) {
                let coord = api.coord([api.value(0), api.value(1)])
                let size = api.size([1, api.value(1)])
                let width = size[0] * 0.35
                let offset = 0
                if (self._isCenter(self.humidityMap, api.value(0))) {
                  offset = -width / 2
                } else {
                  offset = -size[0] * 0.10 / 2 - width
                }
                return {
                  type: 'rect',
                  shape: {
                    x: coord[0] + offset,
                    y: coord[1],
                    width: width,
                    height: size[1]
                  },
                  style: api.style()
                }
              }
            }
          }
          this.chart.setOption(options)
        })
      }
    },
    setCertificateMapChartData () {
      const self = this
      if (self.certificateMap) {
        options.series.map(item => {
          if (item.name === '持证上岗') {
            item = {
              name: '持证上岗',
              type: 'line',
              data: self.certificateMap,
              itemStyle: {
                normal: {
                  color: '#ffb017'
                }
              }
            }
          }
          this.chart.setOption(options)
        })
      }
    }

  },
  watch: {
    certificateMap: {
      handler (newValue, oldValue) {
        for (let i = 0; i < newValue.length; i++) {
          if (oldValue[i] != newValue[i]) {
            this.setCertificateMapChartData()
          }
        }
      },
      deep: true
    },
    temperatureMap: {
      handler (newValue, oldValue) {
        for (let i = 0; i < newValue.length; i++) {
          if (oldValue[i] !== newValue[i]) {
            this.setTemperatureMapChartData()
          }
        }
      },
      deep: true
    },
    humidityMap: {
      handler (newValue, oldValue) {
        for (let i = 0; i < newValue.length; i++) {
          if (oldValue[i] !== newValue[i]) {
            this.setHumidityMapChartData()
          }
        }
      },
      deep: true
    }
  }

}
</script>

<style lang="less" scoped>
.chart-box{
  width: 100%;
  height:100%
}
</style>
